<template>
    <div>
        <loader :status="is_saving" />
        <b-row class="m-0">
            <b-col cols="1" class="text-center p-2 bg-dark text-white d-flex align-items-center justify-content-center">
                <font-awesome-icon icon="map-marker" class="h2 mb-0" />
            </b-col>
            <b-col class="p-3 bg-facebook text-white d-flex align-items-center">
                <div>
                    <h4 class="mb-0"><strong>{{barangay.name}}</strong> </h4>
                    <small class="text-muted text-uppercase font-weight-bold">BARANGAY</small>
                </div>
            </b-col>
        </b-row>
        <CCardGroup class="mb-4">
            <CWidgetProgressIcon
                    :header="formatNumber(Number(barangay.voters_count))"
                    text="Registered Voters"
                    color="info"
                    :value="100"
            >
                <CIcon name="cil-people" height="36"/>
            </CWidgetProgressIcon>
            <CWidgetProgressIcon
                    :header="formatNumber(Number(barangay.members_count))"
                    text="Total Members"
                    :value="Number(barangay.members_count)"
                    color="success"
            >
                <CIcon name="cil-userFollow" height="36"/>
            </CWidgetProgressIcon>
            <!--<CWidgetProgressIcon-->
            <!--header="1238"-->
            <!--text="Products sold"-->
            <!--color="warning"-->
            <!--&gt;-->
            <!--<CIcon name="cil-basket" height="36"/>-->
            <!--</CWidgetProgressIcon>-->
            <CWidgetProgressIcon
                    :header="formatNumber(Number((barangay.members_count/barangay.voters_count)*100),2)+'%'"
                    text="Total Percentage"
                    :value="Number((barangay.members_count/barangay.voters_count)*100)"
            >
                <CIcon name="cil-chartPie" height="36"/>
            </CWidgetProgressIcon>
            <CWidgetProgressIcon
                    :header="'₱ '+formatNumber(Number(est_budget))"
                    text="Total Est. Budget"
                    color="warning"
                    :value="100"
            >
                <CIcon name="cil-tags" height="36"/>
            </CWidgetProgressIcon>
        </CCardGroup>
        <b-tabs content-class="mt-3">
            <b-tab title="Barangay Master List" active>
                <barangay-members-table
                        :fields="table_settings.fields"
                        :api_url="table_settings.api_url"
                        :delete_url="table_settings.delete_url"
                        :top_right_btn_redirect="false"
                        :id="id"
                />
            </b-tab>
            <b-tab title="Budget">
                <div class="progress-group" v-for="(member_type, index) in member_types">
                    <div class="progress-group-header">
                        <CIcon name="cil-user" class="progress-group-icon"/>
                        <span class="title">{{member_type.name}}</span>
                        <span class="ml-auto font-weight-bold">
                            ₱ <number-format-component :currency="false" :input_value="Number(member_type.members_count*member_type.estimated_budget)" :decimal="0"/>
                            <small>(<number-format-component :currency="false" :input_value="Number(((member_type.members_count*member_type.estimated_budget)/est_budget)*100)" :decimal="0"/> %)</small>
                          </span>
                    </div>
                    <div class="progress-group-bars">
                        <CProgress
                                :value="Number(((member_type.members_count*member_type.estimated_budget)/est_budget)*100)"
                                :color="member_type.color"
                        />
                    </div>
                </div>
            </b-tab>
        </b-tabs>
    </div>
</template>
<script>
    import SwalMixin from '@/views/mixins/Swal.js'
    import Loader from '@/views/helpers/LoaderComponent.vue';
    import BarangayMembersTable from './BarangayMembersTable.vue';
    import NumberFormatComponent from '@/views/helpers/NumberFormatComponent.vue';
    import { assets_path } from "@/config";
    import {bus} from '@/main.js';
    export default {
        mixins:[SwalMixin],
        components: {Loader,BarangayMembersTable,NumberFormatComponent},
        props:{
            id: {
                type: Number,
                default: -1
            },
        },
        data() {
            return {
                assets_path: {},
                is_saving:false,
                barangay:{},
                est_budget:0,
                table_settings:{
                    api_url:'barangays/get-voters',
                    delete_url:'',
                    fields:[
                        {key: 'precint.name', label:'PRECINT'},
                        {key: 'name', label:'NAME'},
                        {key: 'address', label:'ADDRESS'},
                        {key: 'member_type.name', label:'TYPE'},
                        {key: 'total_child', label:'MEMBERS'}
                    ]
                },
                member_types:[]
            }
        },
        computed:{
        },
        created(){
            this.assets_path = assets_path;
            this.get();
            this.getReportsPerMemberType();
        },
        methods: {
            get(){
                this.is_saving=true;
                this.membersTableKey++;
                this.axios.get('barangays/get',{params:{id:this.id}}).then(response => {
                    //console.log(result)
                    this.barangay = response.data;
                    this.is_saving=false;
                }).catch(error => console.log(error));
            },

            formatNumber(input_value,decimal=0){
                return input_value.toLocaleString('en-US',{ minimumFractionDigits: decimal,maximumFractionDigits:decimal });
            },

            getReportsPerMemberType(){
                this.axios.get('/barangays/per-member-type',{params:{id:this.id}}).then(response => {
                    this.member_types= response.data;

                    let est_budget = this.member_types.map(item => item.estimated_budget * item.members_count);

                    this.est_budget = est_budget.reduce((sum, value) => sum + value, 0);
                }).catch(error => console.log(error));
            },
        }
    }
</script>