<template>
  <div class="animated fadeIn">
    <b-tabs content-class="bg-white" class="bg-white">
      <b-tab active>
        <template #title>
          <span class="d-md-down-none">All</span>
        </template>
        <per-barangay-table
                :fields="table_settings.fields"
                :api_url="table_settings.api_url"
                :delete_url="table_settings.delete_url"
                ref="displayTable"
                :top_right_btn_redirect="false"
        />
      </b-tab>
    </b-tabs>
    <b-modal
            v-model="voter_modal"
            centered
            size="xl"
            body-class="p-0"
            :hide-footer="true"
            :hide-header="true"
            modal-class="xxl-modal"
            @hide="refresh()"
    >
      <div class="card bg-light mb-0">
        <div class="card-header bg-dark text-white"
        >
          <b-row>
            <b-col>
              <h6 class="mb-0">Barangay Details</h6>
            </b-col>
            <b-col class="text-right">
              <b-button variant="secondary" size="sm" @click="openPrintPage">
                <font-awesome-icon icon="print" /> Print
              </b-button>
            </b-col>
          </b-row>
        </div>
        <div class="card-body bg-white" style="overflow-x: auto;">
          <barangay :id="id"/>
        </div>
      </div>
    </b-modal>
  </div>
</template>
<script>
    import {bus} from '@/main.js';
    import SwalMixin from '@/views/mixins/Swal.js'
    import { server_details } from "@/config";
    import PerBarangayTable from './PerBarangayTable.vue';
    import {client_details,assets_path} from '@/config';
    import Barangay from './Barangay.vue';


    export default {
      mixins:[SwalMixin],
      components: {PerBarangayTable,Barangay},
      data: () => {
          return {
            table_settings:{
              api_url:'barangays/all',
              delete_url:'',
              fields:[
                {key: 'name', label:'BARANGAY'},
                {key: 'percentage', label:'PERCENTAGE'}
              ]
            },
            assets_path:{},
            voter:{},
            id:-1,
            voter_modal:false
          }
      },
      created(){

        this.assets_path = assets_path;

        bus.$on('voterDbClick', (data)=>{
          this.voter_modal=true;
          this.id=data.id;
        });
      },
      methods: {
        refresh(){
          this.$refs.displayTable.getAll();
        },
        openPrintPage() {
          const printUrl = '/print/per-barangay/'+this.id; // Replace with your actual print page URL
          window.open(printUrl, '_blank');
        }
      }
    }
</script>